import { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "animate.css";
import { Navbar, Nav, Container, Button, ButtonGroup, Modal } from "react-bootstrap";
import logo from "../assets/img/logos/logoNN.svg";
import linkedinIcon from "../assets/img/logos/linkedin-icon.svg";
import githubIcon from "../assets/img/logos/github-icon.svg";
import linkedinIconLight from "../assets/img/logos/light/linkedin-icon-light.svg";
import discordIconLight from "../assets/img/logos/light/discord-icon-light.svg";
import discordIcon from "../assets/img/logos/discord-icon.svg";
import flagIconUS from "../assets/img/flags/us.svg";
import flagIconFR from "../assets/img/flags/fr.svg";

const openInNewTab = (url) => {
  window.open(url, "_blank", "noopener,noreferrer");
};

export const NavBar = (props) => {
  const [activeLink, setActiveLink] = useState("home");
  const [scrolled, setScrolled] = useState(false);
  const [modalShow, setModalShow] = useState(false);
  // const [navBarCollapse, setNavBarCollapse] = useState(false);
  const [navBarExpand, setNavBarExpand] = useState(false);

  useEffect(() => {
    const onScroll = (e) => {
      setNavBarExpand(false);
      if (window.scrollY > 50) setScrolled(true);
      else setScrolled(false);

      var myElementH = document.getElementById("home");
      var myElementHeightH = myElementH.offsetHeight;
      var myElementWidthH = myElementH.offsetWidth;
      var boundingH = myElementH.getBoundingClientRect();

      if (
        boundingH.top >= -myElementHeightH &&
        boundingH.left >= -myElementWidthH &&
        boundingH.right <=
          (window.innerWidth / 2.5 || document.documentElement.clientWidth / 2.5) +
            myElementWidthH &&
        boundingH.bottom <=
          (window.innerHeight / 2.5 || document.documentElement.clientHeight / 2.5) +
            myElementHeightH
      )
        setActiveLink("home");

      var myElement = document.getElementById("projects");
      var myElementHeight = myElement.offsetHeight;
      var myElementWidth = myElement.offsetWidth;
      var bounding = myElement.getBoundingClientRect();

      if (
        bounding.top >= -myElementHeight &&
        bounding.left >= -myElementWidth &&
        bounding.right <=
          (window.innerWidth / 2.5 || document.documentElement.clientWidth / 2.5) +
            myElementWidth &&
        bounding.bottom <=
          (window.innerHeight / 2.5 || document.documentElement.clientHeight / 2.5) +
            myElementHeight
      )
        setActiveLink("projects");

      var myElementS = document.getElementById("skills");
      var myElementHeightS = myElementS.offsetHeight;
      var myElementWidthS = myElementS.offsetWidth;
      var boundingS = myElementS.getBoundingClientRect();

      if (
        boundingS.top >= -myElementHeightS &&
        boundingS.left >= -myElementWidthS &&
        boundingS.right <=
          (window.innerWidth / 2.5 || document.documentElement.clientWidth / 2.5) +
            myElementWidthS &&
        boundingS.bottom <=
          (window.innerHeight / 2.5 || document.documentElement.clientHeight / 2.5) +
            myElementHeightS
      )
        setActiveLink("skills");
    };
    window.addEventListener("scroll", onScroll);
  });

  function scrollToTopic(value) {
    const id = value;
    const yOffset = -125;
    const element = document.getElementById(id);
    const y = element.getBoundingClientRect().top + window.pageYOffset + yOffset;
    window.scrollTo({ top: y, behavior: "smooth" });
  }

  function MyVerticallyCenteredModal(props) {
    return (
      <Modal {...props} size="lg" centered>
        <div
          style={{
            border: "solid white 5px",
            borderRadius: "15px",
            boxShadow: "0 0 20px 10px rgba(248, 248, 248, 0.65)",
          }}
        >
          <Modal.Header closeButton></Modal.Header>
          <Modal.Body style={{ textAlign: "center" }}>
            <br />
            <a
              style={{
                color: "black",
                backgroundColor: "rgba(0, 0, 0, 0.15)",
                textDecoration: "none",
                border: "solid 2px black",
                borderRadius: "5px",
                padding: "10px 10px",
                boxShadow: "0px 3px 25px 4px rgba(0, 0, 0, 0.65)",
              }}
              href="mailto:nicolas.nieddu@free.fr"
            >
              nicolas.nieddu@free.fr
            </a>
            <br />
            <div style={{ color: "black" }}>
              <a href="https://discord.gg/296KHYVkYu" style={{ margin: "10px" }}>
                <img
                  style={{ width: "30px" }}
                  src={discordIconLight}
                  alt="Profil discord de Nicolas NIEDDU"
                />
              </a>
              <a
                style={{ margin: "10px" }}
                href="https://www.linkedin.com/in/nicolas-nieddu-71406b192/"
              >
                <img
                  style={{ width: "30px" }}
                  src={linkedinIconLight}
                  alt="Profil linkedin de Nicolas NIEDDU"
                />
              </a>
              <br />
              <span>{props.translate.navbar.modal.myCv} :&nbsp;&nbsp;</span>
              <a
                style={{ margin: "10px", color: "black" }}
                href="https://nnieddu.github.io/cv/index.html"
              >
                {props.translate.navbar.modal.Webversion}
              </a>
              <a
                style={{ margin: "10px", color: "black" }}
                href="https://nnieddu.github.io/cv.pdf"
              >
                PDF
              </a>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="dark" onClick={props.onHide}>
              {props.translate.navbar.modal.closeBtn}
            </Button>
          </Modal.Footer>
        </div>
      </Modal>
    );
  }

  return (
    <Navbar
      expand="lg"
      variant="dark"
      fixed="top"
      className={scrolled ? " scrolled" : ""}
      expanded={navBarExpand}
    >
      <Container>
        <Navbar.Brand
          onClick={() => {
            scrollToTopic("home");
          }}
        >
          {<img id="logoNN" src={logo} alt="Logo Nicolas NIEDDU" />}
        </Navbar.Brand>
        <Nav.Link
          className={activeLink === "home" ? "active navbar-link" : "navbar-link"}
          onClick={() => {
            scrollToTopic("home");
          }}
        >
          {props.translate.navbar.home}
        </Nav.Link>
        <Nav.Link
          className={activeLink === "projects" ? "active navbar-link" : "navbar-link"}
          onClick={() => {
            scrollToTopic("projects");
          }}
        >
          {props.translate.navbar.projects}
        </Nav.Link>
        <Nav.Link
          className={activeLink === "skills" ? "active navbar-link" : "navbar-link"}
          onClick={() => {
            scrollToTopic("skills");
          }}
        >
          {props.translate.navbar.skills}
        </Nav.Link>
        <Navbar.Toggle onClick={() => setNavBarExpand(!navBarExpand)} />
      </Container>

      <Navbar.Collapse id="responsive-navbar-nav">
        <Container fluid></Container>
        <Nav>
          <Container>
            <ButtonGroup>
              <Button variant="link" className="social-icon">
                <p
                  className="dontAnimOnLangChange"
                  onClick={() => openInNewTab("https://github.com/nnieddu")}
                >
                  <img src={githubIcon} alt="Profil Github de Nicolas NIEDDU" />
                </p>
              </Button>
              <Button variant="link" className="social-icon">
                <p
                  className="dontAnimOnLangChange"
                  onClick={() => openInNewTab("https://discord.gg/296KHYVkYu")}
                >
                  <img src={discordIcon} alt="Profil discord de Nicolas NIEDDU" />
                </p>
              </Button>
              <Button variant="link" className="social-icon">
                <p
                  className="dontAnimOnLangChange"
                  onClick={() =>
                    openInNewTab("https://www.linkedin.com/in/nicolas-nieddu-71406b192/")
                  }
                >
                  <img src={linkedinIcon} alt="Profil linkedin de Nicolas NIEDDU" />
                </p>
              </Button>
              <button
                className="buttonCustm"
                onClick={() => {
                  setModalShow(true);
                  setNavBarExpand(false);
                }}
              >
                {props.translate.navbar.contactMeBtn}
              </button>
              <MyVerticallyCenteredModal
                show={modalShow}
                translate={props.translate}
                onHide={() => setModalShow(false)}
              />
              <button
                className="buttonCustm"
                onClick={() => {
                  props.setLang();
                  setNavBarExpand(false);
                }}
              >
                <img
                  className="flag"
                  src={props.lang === "ENG" ? flagIconUS : flagIconFR}
                  alt="Language flag icon"
                />
              </button>
            </ButtonGroup>
          </Container>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};
