import React from "react";

/**
 * Props available :
 * @param delay {integer} integer, in ms, lower is faster, default = 1200
 * @param speed {integer} integer, in ms, lower is faster, default = 280
 * @param data {string} string to display
 */
class TypeWriter extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      text: "",
      data: props.data,
      hello: props.hello,
    };
    props.delay ? (this.delay = props.delay) : (this.delay = 1200);
    props.speed ? (this.speed = props.speed) : (this.speed = 280);
    this.tick = this.tick.bind(this);
  }

  componentDidMount() {
    this.unmounted = false;
    setTimeout(() => {
      this.tick();
    }, this.delay);
  }

  componentDidUpdate(prevProps) {
		if (prevProps.hello !== this.props.hello) {
			this.setState({ hello: this.props.hello });
			this.setState({ text: this.props.data });
    }
  }

  tick() {
    if (this.unmounted) {
      return;
    }

    const fullTxt = this.props.data;
    let newText = "";
    let delta = Math.random() * this.speed;
    newText = fullTxt.substring(0, this.state.text.length + 1);

    if (newText.length === this.state.text.length) this.unmounted = true;
    this.setState({ text: newText });

    setTimeout(() => {
      this.tick();
    }, delta);
  }

  render() {
    return (
      <span>
        <span style={{ whiteSpace: "pre-wrap" }} id="helloTypeW">
          {this.state.hello}
        </span>
        <span id="contentTypeW">{this.state.text}</span>
        <span className="typewriter"></span>
      </span>
    );
  }
}

export default TypeWriter;