import { Container, Row, Col } from "react-bootstrap";
import "animate.css";
import TypeWriter from "./Typewriter";
// import pp from "../assets/img/pp.webp";
import pp from "../assets/img/pp.jpg";

export const Home = (props) => {
  const today = new Date();
  const birthDate = new Date("1994/06/06");
  var age = today.getFullYear() - birthDate.getFullYear();
  const month = today.getMonth() - birthDate.getMonth();

  if (month < 0 || (month === 0 && today.getDate() < birthDate.getDate())) {
    age--;
  }

  return (
    <Container id="home">
      <Col size={12}>
        <h1 className="dontAnimOnLangChange animate__animated animate__slideInLeft">
          <TypeWriter
            hello={props.translate.home.hello}
            data={props.translate.home.typeWriterTxt}
          />
            <img src={pp} alt="Nicolas portrait" />
        </h1>
      </Col>
      <Row >
        <p
          style={{ whiteSpace: "pre-wrap" }}
          className="animate__animated animate__slideInRight"
        >
          {props.translate.home.biographie1} {age} {props.translate.home.biographie2}{" "}
          <strong>{props.translate.home.biographieStrong}</strong>
        </p>
      </Row>
    </Container>
  );
};
