import "./styles/App.css";
import { NavBar } from "./components/NavBar";
import { Home } from "./components/Home";
import { Projects } from "./components/Projects";
import { Skills } from "./components/Skills";
import { Bonus } from "./components/Bonus";
import { Footer } from "./components/Footer";
import warpspeed from "./assets/utils/warpspeed.js";
import { useEffect, useState } from "react";
import Earth from "./components/Planet";
import Greetings from "./components/Greetings";

function App() {
  var ourStorage = window.localStorage;
  const [language, setLang] = useState(
    ourStorage.getItem("lang") ? ourStorage.getItem("lang") : "FR"
  );
  ourStorage.setItem("lang", language);
  const [translations, setTranslations] = useState(
    ourStorage.getItem("lang")
      ? require("./translations/" + ourStorage.getItem("lang") + ".json")
      : require("./translations/ENG.json")
  );

  useEffect(() => {
    new warpspeed("stars-bg-canvas");
  }, [ourStorage]);

  function setLanguage() {
    let textElements = document.querySelectorAll("a, p, h1, h2, span, button");
    textElements.forEach(function (element) {
			if (!element.classList.contains("navbar-brand") && !element.classList.contains("social-icon") && !element.classList.contains("dontAnimOnLangChange"))
				{
					element.style.transition = "0s";
					element.style.opacity = 0;
				}
    });
    setTimeout(() => {
			textElements.forEach(function (element) {
				if (!element.classList.contains("navbar-brand") && !element.classList.contains("social-icon") && !element.classList.contains("dontAnimOnLangChange"))
				{
					element.style.transition = "1s";
					element.style.opacity = 1;
				}
			});
    }, 1);

		const elements = document.getElementsByClassName('typewriter')[0].style.display = "none";
		// for (let i = 0; i < elements.length; i++) {
		// 	elements[i].style.opacity = '0';
		// }

		language === "ENG" ? setLang("FR") : setLang("ENG");
    language === "ENG"
      ? setTranslations(require("./translations/FR.json"))
      : setTranslations(require("./translations/ENG.json"));
  }

  return (
    <div>
      <NavBar translate={translations} lang={language} setLang={setLanguage} />
      <Home translate={translations} lang={language} />
      <Earth />
      <Projects translate={translations} />
      <Skills translate={translations} />
      <Bonus translate={translations} />
      <Greetings translate={translations} />
      <Footer lang={language} translate={translations} />
      <div id="stars-bg-div">
        <canvas id="stars-bg-canvas"></canvas>
      </div>
    </div>
  );
}

export default App;
